.form_profile {
	.form__fieldset_profile {
		legend::before {
			background-image: url(data:image/svg+xml;base64,);
		}
		.form__item {
			max-width: 520/16+em;
			margin-left: auto;
			margin-right: auto;
		}
		select {
			max-width: 320/20+em;
			margin-left: auto;
			margin-right: auto;
		}
	}
	.form__fieldset_expirience {
		text-align: left;
		margin-bottom: 7.5em;
		legend::before {
			background-image: url(data:image/svg+xml;base64,);
		}
		.form__item {
			display: inline-block;
			vertical-align: middle;
			&:nth-of-type(3n+1),
			&:nth-of-type(3n+2) {
				width: 36%;
			}
			@media screen and (max-width: 600px) {
				&:nth-child(n) {
					width: 45%;
					padding-left: 2.5em;
				}
			}
			@media screen and (max-width: 450px) {
				&:nth-child(n) {
					display: block;
					width: 100%;
					padding-left: 2.5em;
				}
			}
		}
	}
	.form__fieldset_js {
		margin-bottom: 6.5em;
		legend::before {
			background-image: url(data:image/svg+xml;base64,);
		}
		.range-value {
			display: none;
		}
	}
	.form__fieldset_about {
		legend {
			margin-bottom: 1.15em;
			div {
				font-size: 16/30+em;
				max-width: 506/16+em;
				margin-left: auto;
				margin-right: auto;
				margin-top: .85em;
				line-height: 1.6;
			}
			&::before {
				background-image: url(data:image/svg+xml;base64,);
			}
		}
		.form__item_textarea {
			margin-left: auto;
			margin-right: auto;
			max-width: 510/16+em;
		}
	}
}
.header {
	background-color: #ffffff;
  padding-top: 2.15em;
  padding-bottom: 1.85em;
	@media screen and (max-width: 768px) {
		height: 0;
		padding: 0;
	}
	&__nav {
		font-size: .875em;
    font-weight: 300;
		ul {
			list-style: none;
			margin: 0;
			padding: 0;
			display: flex;
			justify-content: center;
			align-items: baseline;
			@media screen and (max-width: 768px) {
				position: fixed;
				width: 100vw;
				background-color: #ffffff;
				top: 0;
				left: 0;
				box-shadow: 0 .15em 1.5em rgba(0, 0, 0, .25);
				padding-top: 2.5em;
				padding-bottom: 1.5em;
				display: block;
				font-size: 1.5em;
				z-index: 14;
				transition: all .5s ease-out;
				transform: translateY(-100%);
				visibility: hidden;
				opacity: 0;
				will-change: transform, opacity, visibility;
				&.active {
					transform: translateY(0);
					visibility: visible;
					opacity: 1;
				}
			}
		}
		li {
      margin-left: 2.65em;
      margin-right: 2.65em;
			@media screen and (max-width: 768px) {
				margin-left: auto;
				margin-right: auto;
				width: 100%;
				max-width: 280px;
				margin-bottom: .7em;
			}
		}
		a {
      display: block;
			color: #435993;
      line-height: 1.1;
			text-decoration: none;
      border-bottom: 1px solid transparent;
      transition: border-color .3s ease, color .2s ease;
			&.active {
				color: #1c1c1c;
			}
      &:hover {
        border-bottom-color: currentColor;
      }
      &:active {
        color: #1c1c1c;
      }
			@media screen and (max-width: 768px) {
				display: inline-block;
			}
		}
		
	}
	&__burger {
		display: none;
		@media screen and (max-width: 768px) {
			display: block;
			@include burger-button(28px, 4px, #ffffff);
			box-shadow: 0 0 4em rgba(#ffffff, 1);
			position: fixed;
			top: 15px;
			right: 15px;
			z-index: 15;
		}
	}
}